import Vue from "vue"
import App from "./App.vue"
import router from "@/router/router"
import store from "@/store/store"

import Vue2Storage from 'vue2-storage'
import FlashMessage from "@smartweb/vue-flash-message";
import VModal from "vue-js-modal"
import { i18n } from "@/lang/lang.js"
const fb = require("@/firebase/Firebase");

require("bootstrap");
import "popper.js";

import "bootstrap/dist/css/bootstrap.min.css";
import "@/assets/css/color/default.css";
import "@/assets/css/app.css";

Vue.use(Vue2Storage, {
  prefix: 'app_',
  driver: 'local',
  ttl: 60 * 60 * 24 * 30 * 3 * 1000 // 3 months
});

Vue.use(FlashMessage, {
	messageOptions: {
		time: 600,
		icon: true
	}
});
Vue.use(VModal, { dialog: true });

Vue.prototype.$databaseURL = "https://colour-assisted-learning.firebaseio.com/patterns.json";
Vue.config.productionTip = false;

let app;
fb.auth.onAuthStateChanged(() => {
	if(! app )	
	{
		app = new Vue({
			router,
			i18n,
			store,
			render: h => h(App)
		}).$mount("#colour-aided-learning-app")
	}
});