<template>
    <div>
        <div id="loading" v-show="loading">
            <div class="load-circle"><span class="one"></span></div>
        </div>

        <nav class="navbar navbar-expand-md navbar-light fixed-top bg-white">
            <router-link to="/" class="navbar-brand"><img alt="ColorSyn" src="./assets/logo.png" /></router-link>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="section-titel ml-md-auto">
                <h6 class="theme-after dark-color">{{ $t('appName') }} - {{ $t('appSlogan') }}</h6>
            </div>
            <ul class="navbar-nav flex-row ml-md-auto d-none d-md-flex">
                <li class="nav-item"><a class="nav-link p-2" href="#" v-on:click.prevent="toggleInstructionsDialog">?</a></li>
                <li v-if="!loggedIn" class="nav-item"><a class="nav-link p-2" href="#" v-on:click.prevent="toggleLoginDialog">Login</a></li>
                <li v-if="loggedIn" class="nav-item"><a class="nav-link p-2" href="#" v-on:click.prevent="logout">Logout</a></li>
                <li class="nav-item dropdown">
                    <a class="nav-item nav-link dropdown-toggle mr-md-2" href="#" id="bd-versions" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      Menu
                  </a>
                  <div class="btn dropdown-menu dropdown-menu-right" aria-labelledby="bd-versions">
                    <router-link class="dropdown-item" to="/">Home</router-link>
                    <router-link class="dropdown-item" to="/about">About</router-link>
                    <router-link class="dropdown-item" to="/objective">Objective</router-link>
                    <router-link class="dropdown-item" to="/feedback">Feedback</router-link>
                    <router-link v-if="loggedIn" class="dropdown-item" to="/configs">Configs</router-link>
                </div>
            </li>
        </ul>
        <app-language-switcher cssClass="m-30px-r"></app-language-switcher>
    </nav>

    <div id="pages-stack" class="pages-stack">
        <router-view></router-view>
    </div>

    <v-dialog name="colosyn-help"></v-dialog>

    <modal name="login-modal" draggable="true" height="auto">
        <app-login-box/>
    </modal>

    <FlashMessage></FlashMessage>

</div>
</template>

<script>
    import languageSwitcher from '@/components/LanguageSwithcher.vue';
    import login from '@/components/Login.vue';
    import firebase from "firebase/app"
    import "firebase/auth";

    export default {
        components: {
            'app-language-switcher': languageSwitcher,
            'app-login-box': login
        },
        data: function() {
            return {
                loading: true
            }
        },
        created: function() {
            this.loading = false;
        },
        methods: {
            toggleLoginDialog: function() {
                this.$modal.show('login-modal');
            },
            toggleInstructionsDialog: function() {
                this.$modal.show('dialog', { text:  this.$i18n.messages[this.$i18n.locale].instructions });
            },
            logout: function() {
                let _this = this;
                firebase.auth().signOut().then(()=>{
                    _this.$router.replace('/');
                });
            }
        },
        computed: {
            loggedIn: function() {
                return firebase.auth().currentUser;
            }
        }
    }
</script>
