<template>
    <span v-bind:class="cssClass">
        <a v-for="lang in languages" :key="lang" class="p-1 bordered" :class="lang" v-on:click.prevent="setLanguage(lang)" href="#">{{ lang }}</a>
    </span>
</template>

<script>
    export default {
        props: {
            cssClass: {
                required: true,
                type: String
            }
        },
        data: function() {
            return {
               languages: ['en', 'fr']
            }
        },
        methods: {
            setLanguage: function(lang) {
                if( ! ['en', 'fr'].includes(lang) || this.$i18n.locale == lang ) return false;
                
                let _this = this;
                import(`@/lang/locale/${lang}.json`).then(function(msg) {
                    _this.$i18n.setLocaleMessage(lang, msg.default || msg);
                    _this.$i18n.locale = lang;
                });
                this.flashMessage.success({ message: 'Website language changed to '+  (lang == 'en' ? 'english' : 'french') +'.'});
            }
        }
    }
</script>
