<template>
    <div class="login p-20px">
        <div v-if="! showForgotPassword">
            <p class="lead">LOGIN</p>
            <p v-if="errors.length">
                <b>Please correct the following error(s):</b>
                <ul>
                    <li v-for="error in errors">{{ error }}</li>
                </ul>
            </p>
            <form autocomplete="false">
                <div class="form-group">
                    <input type="email" v-model.trim="loginForm.email" class="form-control" required placeholder="Email">
                </div>
                <div class="form-group">
                    <input type="password" v-model.trim="loginForm.password" class="form-control" required placeholder="Password">
                </div>
<!--                 <div class="form-group">
                    <a href="#" @click="togglePasswordReset">Reset Password</a>
                </div> -->
            </form>
            <button class="btn btn-theme" @click="login">Sign In</button>
        </div>
        <div>
            <form v-if="showForgotPassword" @submit.prevent class="password-reset">
                <p class="lead">Reset Password</p>
                <div v-if="!passwordResetSuccess">
                    <p>We will send you an email to reset your password</p>

                    <div class="form-group">
                        <input v-model.trim="passwordForm.email" type="email" class="form-control" placeholder="you@email.com"/>
                    </div>
                    <div class="form-group">
                        <button @click="resetPassword" class="btn btn-theme">Submit</button>
                    </div>
                    <div class="form-group">
                        <a href="#" @click="togglePasswordReset">Back to Log In</a>
                    </div>
                </div>
                <div v-if="passwordResetSuccess">
                    <h1>Email Sent</h1>
                    <p>check your email for a link to reset your password</p>
                    <div class="form-group">
                        <a href="#" @click="togglePasswordReset">Back to Log In</a>
                    </div>                    
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    const fb = require("@/firebase/Firebase");
    export default {
        data: function() {
            return {
                errors: [],
                showForgotPassword: false,
                passwordResetSuccess: false,
                loginForm: {
                    password: '',
                    email: ''
                },
                passwordForm: {
                    email: ''
                }
            }
        },
        methods: {
            togglePasswordReset: function() {
                return this.showForgotPassword =  ! this.showForgotPassword;
            },
            login: function() {
                let _this = this;
                if ( this.checkForm() )
                {  
                    fb.auth.signInWithEmailAndPassword(this.loginForm.email, this.loginForm.password).then(function(user) {
                        fb.db.collection('users').doc(user.user.uid).get().then(res => {
                           if(res.data().active) 
                           {
                                _this.$router.replace('configs');
                                _this.$modal.hide('login-modal');
                                _this.flashMessage.success({ message: 'You have logged in successfully.'});
                           }
                           else
                           {
                                _this.errors.push('Your account is deativated. Please contact the administrator.');
                                _this.flashMessage.error({ message: 'Your account is deativated. Please contact the administrator.'});
                           }
                        }).catch(err => {
                            console.log(err)
                        });
                    },
                    function(err) {
                            _this.flashMessage.error({ message: 'There is no user record corresponding to the email and password provided. Please try again.'});
                            
                            _this.errors.push("There is no user record corresponding to the email and password provided. Please try again.");
                            console.log("Error occured "+ err.message);
                        }
                        );
                }               
            },
            resetPassword: function() {
                if(this.validEmail(this.passwordForm.email)) {
                    this.errors.push('Valid email required.');
                    return false;
                }
                let _this = this;
                fb.auth.sendPasswordResetEmail(this.passwordForm.email).then((res) => {
                    _this.passwordForm.email = ''
                    _this.passwordResetSuccess = true;
                    console.log(res)
                }).catch(err => {
                    console.log(err);
                    this.errors.push(err.message);
                })
            },
            checkForm: function () {
                this.errors = [];

                if( !this.loginForm.password )
                    this.errors.push('Password is required.');

                if (!this.loginForm.email || ! this.validEmail(this.loginForm.email))
                    this.errors.push('Valid email required.');

                return (! this.errors.length) ? true : false;
            },
            validEmail: function (email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            }            
        }
    }
</script>
