import Vue from "vue";
import Router from "vue-router";
import { i18n } from "@/lang/lang.js";
import firebase from "firebase/app"
import "firebase/auth";

Vue.use(Router);

const router = new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes: [
    {
        path: "/",
        name: "home",
        component: () => import("@/views/Home.vue")
    },
    {
        path: "/about",
        name: "about",
        component: () => import("@/views/About.vue")
    },
    {
        path: "/objective",
        name: "objective",
        component: () => import("@/views/Objective.vue")
    },
    {
        path: "/feedback",
        name: "feedback",
        component: () => import("@/views/Comments.vue")
    },
    {
        path: "/configs",
        name: "configs",
        component: () => import("@/views/admin/ConfigPage.vue"),
        meta: {
            requiresAuth: true
        }
    },
    {
        path: "/not-found",
        name: "not found",
        component: () => import("@/views/NotFound.vue")
    }
    ]
});

router.beforeEach((to, from, next) => {
    if (! to.matched.length) return next('/not-found');

    const lang = i18n.locale;
    import(`@/lang/locale/${lang}.json`).then(function(msg) {
        i18n.setLocaleMessage(lang, msg.default || msg);
        i18n.locale = lang;
    });

    const currentUser = firebase.auth().currentUser;
    const requiresAuth = to.matched.some(r => r.meta.requiresAuth);
    if(requiresAuth && ! currentUser) 
        return next({ path: '/' });
    // else if(! requiresAuth && currentUser) 
    //     return next({ path: '/configs' });
    else 
        return next();
});

export default router;